.container {
    max-width: 950px;
    margin: 0 auto;
  }
  
  .promo {
    min-height: 650px;
    padding: 21px 0 93px;
    background: url(img/bg1.png) center (center / cover) no-repeat;
    background: red;
    &__wrapper {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        margin-top: 90px;
        
    }
    &__header {
      font-size: 42px;
      color: #ffffff;
      font-weight: 700;
      text-transform: uppercase;
      line-height: 42px;
          }
    &__subheader {
      margin-top: 17px;
      color: #ffffff;
      font-size: 24px;
      font-weight: 500;
      line-height: 24px;
      text-transform: uppercase;
    }
    &__icons {
      margin-top: 65px ;
      width: 425px;
      display: flex;
      justify-content: space-between ;

    }
    &__ico {
      width: 108px;
      text-align: center;
      font-size: 14px;
      font-weight: 400;
      img {
        margin-bottom: 20px;
        margin: 0 auto;
        margin-bottom: 20px;
      }
    

  }
    
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .header__logo {
    width: 250px;
  
    img {
      width: 100%;
    }
  }
  
  .header__Official {
    color: #ffffff;
    font-family: "Museo Sans Cyrl";
    font-size: 12px;
    font-weight: 500;
    text-align: center;
  
    span {
      font-size: 18px;
      display: block;
    }
  }
  
  .header__phon {
    display: block;
    font-size: 18px;
    color: #ffffff;
    font-family: "Museo Sans Cyrl";
    font-weight: 700;
  
    &:hover {
      color: #ffffff;
      text-decoration: none;
      color: #ffffff;
      font-family: "Museo Sans Cyrl";
      text-align: right;
    }
  }
  
  .header__contacts {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    font-size: 18px;
  }
  
  .header .button {
    width: 150px;
    height: 30px;
    border-radius: 5px;
    background-color: #c70101;
    border: none;
    font-size: 12px;
    color: #ffffff;
    font-family: "Museo Sans Cyrl";
    text-align: center;
    font-weight: 500;
  }
}